<template>
		<nav class="navbar navbar-expand-lg ">
			<router-link
				class="navbar-brand"
				:to="{ name: 'home.index' }"
			>
			<eye-new-icon width="50" height="50"></eye-new-icon>
				<span class="brand_name">Red Eye</span>
			</router-link>

			<button
				class="navbar-toggler"
				type="button"
				@click="toggleMenu"
			>
				<span class="navbar-toggler-icon"></span>
			</button>

			<div
				class="collapse navbar-collapse"
				:class="{ show : menuCollapsed}"
			>
				<ul class="navbar-nav mr-auto">
					<router-link
						:to="{ name: 'home.index' }"
						active-class="active"
						class="nav-item"
						tag="li"
					>
						<a class="nav-link">
							{{$t("header.about")}}
						</a>
					</router-link>
					<router-link
						:to="{ name: 'account.index' }"
						active-class="active"
						class="nav-item"
						tag="li"
					>
						<a class="nav-link">
							{{$t("header.contacts")}}
						</a>
					</router-link>
				</ul>
				<div class="lang_change">
					<div class="lang_icon"
					  @click="setLocale('ru')"
					>
						<ru-icon></ru-icon>
					</div>
					<div class="lang_icon"
					  @click="setLocale('eng')"
					>
						<eng-icon></eng-icon>
					</div>
				</div>
			</div>
		</nav>
</template>

<script>
import eyeIcon from "@/components/icons/eye";
import ruIcon from "@/components/icons/ru";
import engIcon from "@/components/icons/eng";
import eyeNewIcon from "@/components/icons/eyeNew";

import Vue from "vue";

export default {
  name: "v-header",
  components: {
    eyeIcon,
		eyeNewIcon,
    ruIcon,
    engIcon
  },
  data() {
    return {
      menuCollapsed: false
    };
  },
  methods: {
    /**
     * Will toggle the menu.
     */
    toggleMenu() {
      this.menuCollapsed = !this.menuCollapsed;
    },
    setLocale: function(locale) {
      Vue.config.lang = locale;
      this.activeLocale = locale;
      // this.$cookie.set("locale", locale);
      this.$i18n.locale = Vue.config.lang;
    }
  }
};
</script>



