<template>
	<svg
	xmlns="http://www.w3.org/2000/svg"
	x="0px"
	y="0px"
	viewBox="0 0 24 24"
	:width="width"
    :height="height"
	>
		<g
		transform="translate(0,-2.5)"
		>
			<path
				d="M 12,8 C 7,8 1,14 1,14 c 0,0 6,6 11,6 5,0 11,-6 11,-6 0,0 -6,-6 -11,-6 z m 0,10 c -2.2,0 -4,-1.8 -4,-4 0,-2.2 1.8,-4 4,-4 2.2,0 4,1.8 4,4 0,2.2 -1.8,4 -4,4 z"
				id="path5"
				fill="#FFF"
				stroke="#000"
				stroke-width=".5px"
			/>

			<circle
			cx="12"
			cy="14"
			r="4"
			fill="#FFF"
			/>

			<circle
			cx="12"
			cy="14"
			r="2"
			fill="red"
			/>
		</g>
	</svg>
</template>

<script>
export default {
  name: "eye-icon",
  props: {
    width: {
      type: [Number, String],
      default: 24
    },
    height: {
      type: [Number, String],
      default: 24
    }
    // iconColor: {
    //   type: String,
    //   default: "currentColor"
    // }
  }
};
</script>


